import 'what-input';

import UIkit from 'uikit';
import Icons from 'uikit/dist/js/uikit-icons';

// loads the Icon plugin
UIkit.use(Icons);


const myVideo = document.querySelector('video');

const playBtn = document.querySelector('.play--video');
const pauseBtn = document.querySelector('.pause--video');

playBtn.addEventListener('click', function (e) {
    e.preventDefault();
    myVideo.play()
});

pauseBtn.addEventListener('click', function (e) {
    e.preventDefault();
    myVideo.pause()
});

document.addEventListener("DOMContentLoaded", function(){
    setTimeout(function () {
        document.querySelector('#introLoader').classList.add('uk-animation-slide-top');
        document.querySelector('#introLoader').classList.add('uk-animation-reverse');
    }, 600)
});
// uk-animation-fade uk-animation-reverse
